<template>
  <div>
    <section class="header-inside"></section>
    <section class="main-content">
      <div class="container">
        <div class="row">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#" @click.prevent="goHome">首頁</a>
              </li>
              <li class="breadcrumb-item">
                <a href="#" @click.prevent="goNews">最新消息</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                {{ NewsDetail["NE_TITLE"] }}
              </li>
            </ol>
          </nav>
        </div>

        <h2 class="text-center">最新消息</h2>

        <div class="news-content">
          <div class="news-title-info">
            <h3>
              {{ NewsDetail["NE_TITLE"] }}
            </h3>
            <div class="date">{{ NewsDetail["NE_DATE"] }}</div>
          </div>
          <div class="news-text">
            {{ (NewsDetail["NE_CONTENT"]) }}
          </div>
          <div class="py-3 text-center">
            <a
              href="#"
              @click.prevent="goNews"
              class="btn btn-success btn-lg"
              role="button"
              aria-pressed="true"
              >返回最新消息列表</a
            >
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      date: "",
      NewsDetail: [],
    };
  },
  mounted() {
    this.GetData();
  },
  methods: {
    goHome() {
      this.$router.push({ path: "/" });
    },
    goNews() {
      this.$router.push({ path: "News" });
    },
    //取得資料
    GetData() {
      const NE_ID = this.$route.query.name["NE_ID"];
      console.log(NE_ID);
      if (NE_ID === undefined) {
        this.$router.push({ path: "News" });
      }
      // axios.get("https://as199629.github.io/LIKE/json/NewsDetail/" + NE_ID + ".json").then((response) => {
      //   console.log(response.data);
      //   this.NewsDetail = response.data;
      // });

      let jsonData = [];
      axios
        .get("https://as199629.github.io/LIKE/json/News.json")
        .then((response) => {
          console.log(response.data);
          jsonData = response.data;
        });
      for (let i = 0; i < jsonData.length; i++) {
        if (jsonData[i].NE_ID === NE_ID) {
          this.NewsDetail = jsonData[i];
        }
      }
    },
    // DateFormatter(item) {
    //   return this.IsNull(item) == ""
    //     ? ""
    //     : item.split("T")[0].replaceAll("-", "/");
    // },
  },
};
</script>